import React, { ChangeEventHandler, FormEventHandler, FunctionComponent, useCallback, useEffect, useRef, useState } from 'react';
import './App.css';
import { ReactComponent as SendIcon } from "./assets/send.svg";
import Canvas from "./canvas";
import { INTRO_PROMPTS, QUESTIONS } from "./data";

const Component: FunctionComponent = () => {
  const [questions, setQuestions] = useState(QUESTIONS);
  const [question, setQuestion] = useState(0);
  const [introPrompt, setIntroPrompt] = useState(0);
  const responseRef = useRef<HTMLTextAreaElement | null>(null);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const animation_style = { animation: `${isTransitioning ? "fade-out" : "fade-in"} 1s ease` };

  const onNextIntroPromptClick = () => {
    setIsTransitioning(true);
  };

  const onStartOverClick = () => {
    window.location.reload();
  };

  const onResponseChange: ChangeEventHandler<HTMLTextAreaElement> = (event) => {
    setQuestions(questions => {
      let newQuestions = [...questions];
      newQuestions[question].response = event.target.value;
      return newQuestions;
    });
  };

  const onNextQuestionSubmit: FormEventHandler = (event) => {
    event.preventDefault();
    if (questions[question].response.length > 200 || questions[question].response.length === 0) return;
    setIsTransitioning(true);
  };

  const onNextIntoAnimationEnd = () => {
    if (!isTransitioning) return;
    setIntroPrompt(prompt => prompt + 1);
    setIsTransitioning(false);
  };

  const onNextQuestionAnimationEnd = () => {
    if (!isTransitioning) return;
    setQuestion(question => question + 1);
    setIsTransitioning(false);
  };

  const setResponseRef = useCallback(node => {
    responseRef.current = node;
    if (!responseRef.current) return responseRef;
    responseRef.current.style.height = "auto";
    responseRef.current.style.height = (responseRef.current.scrollHeight + 2) + "px";
    return responseRef;
  }, []);

  useEffect(() => {
    let resizeHandler = () => {
      if (!responseRef.current) return;
      responseRef.current.style.height = "auto";
      responseRef.current.style.height = (responseRef.current.scrollHeight + 2) + "px";
    };

    window.addEventListener('resize', resizeHandler);
    resizeHandler();

    return () => {
      window.removeEventListener('resize', resizeHandler);
    };
  }, [questions, question]);

  useEffect(() => {
    if (introPrompt >= INTRO_PROMPTS.length) return;

    let keyupHandler = (event: KeyboardEvent) => {
      event.preventDefault();
      onNextIntroPromptClick();
    };

    window.addEventListener('keyup', keyupHandler);

    return () => {
      window.removeEventListener('keyup', keyupHandler);
    };
  }, [introPrompt]);

  return (
    <>
      {introPrompt >= INTRO_PROMPTS.length ? null :
        <div className="intro-prompt-wrapper" style={animation_style} onAnimationEnd={onNextIntoAnimationEnd} onClick={onNextIntroPromptClick}>
          <h1 className="intro-prompt" style={{ cursor: INTRO_PROMPTS[introPrompt].cursor }}>{INTRO_PROMPTS[introPrompt].prompt}</h1>
        </div>
      }
      {(introPrompt < INTRO_PROMPTS.length || question >= QUESTIONS.length) ? null :
        <div className="questions-wrapper" style={animation_style} onAnimationEnd={onNextQuestionAnimationEnd}>
          <Canvas />
          <div className="question-wrapper-y">
            <div className="spacer" style={{ flexBasis: questions[question].y }} />
            <div className="question-wrapper-x">
              <div className="spacer" style={{ flexBasis: questions[question].x }} />
              <form className="question" onSubmit={onNextQuestionSubmit}>
                <img className="question-img"
                  draggable={false}
                  src={questions[question].pic}
                  alt=""
                  width={questions[question].picWidth}
                  style={{ aspectRatio: `${questions[question].picWidth} / ${questions[question].picHeight}` }}
                />
                <div className="question-response">
                  <textarea ref={setResponseRef}
                    className="question-textarea"
                    value={questions[question].response}
                    placeholder={questions[question].prompt}
                    onChange={onResponseChange}
                    maxLength={200} minLength={1} rows={1} />
                  <button type="submit" className="question-send" disabled={questions[question].response.length === 0}><SendIcon /></button>
                </div>
              </form>
            </div>
          </div>
        </div>
      }
      {question < QUESTIONS.length ? null :
        <div className="response-wrapper" style={animation_style}>
          <h1 className="response-title">
            <div>
              <p>We look</p>
              <p>at the same</p>
              <p>photos and</p>
              <p>see such</p>
              <p>different</p>
              <p>things.</p>
            </div>
            <div>
              <p>How</p>
              <p>beautiful.</p>
              <div className="start-over-placeholder">Shall we start again?</div>
              <button className="start-over" onClick={onStartOverClick}>Shall we start again?</button>
            </div>
          </h1>
          <div className="responses">
            {questions.map((question, ind) => <div key={ind} className="response-wrapper-x">
              <div className="spacer" style={{ flexBasis: question.x }} />
              <div className="response">
                <img className="response-img" src={question.pic} alt="" draggable={false} />
                <div className="answer-text-wrapper">
                  <div className="response-group">
                    <p className="answer-user">Brian</p>
                    <p className="answer-text">{question.prompt}</p>
                    <p className="answer-text">{question.answer}</p>
                  </div>
                </div>
                <div className="response-text-wrapper">
                  <div className="response-group">
                    <p className="response-user">You</p>
                    <p className="response-text" style={{ background: `linear-gradient(to right, ${question.gradientStart}, ${question.gradientEnd})` }}>{question.response}</p>
                  </div>
                </div>
              </div>
            </div>)}
          </div>
        </div>
      }
    </>
  );
};

export default Component;
