import { CanvasHTMLAttributes, DetailedHTMLProps, FunctionComponent, useEffect, useRef } from "react";
import * as THREE from 'three';

const Component: FunctionComponent<DetailedHTMLProps<CanvasHTMLAttributes<HTMLCanvasElement>, HTMLCanvasElement>> = ({ className, ...props }) => {
    const canvasRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        if (!canvasRef.current) return;
        let canvas = canvasRef.current;

        let ctx = canvas.getContext('2d');
        if (!ctx) return;

        let start = Date.now();
        let scene = new THREE.Scene();
        let camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        let renderer = new THREE.WebGLRenderer();

        let texture = new THREE.Texture(canvas);
        let geometry = new THREE.PlaneGeometry(camera.aspect, 1, 16, 16);
        let material = new THREE.MeshBasicMaterial({ map: texture, color: 0xFFFFFF });
        let plane = new THREE.Mesh(geometry, material);

        scene.add(plane);

        let Mouse = {
            x: window.innerWidth / 2,
            y: window.innerHeight / 2,
            down: false,
            events: {
                move: (e: MouseEvent) => {
                    Mouse.x = ((e.pageX + Mouse.x) * 0.5) | 0;
                    Mouse.y = ((e.pageY + Mouse.y) * 0.5) | 0;
                },
                down: (e: MouseEvent) => {
                    Mouse.down = !Mouse.down;
                },
                up: (e: MouseEvent) => {
                    Mouse.down = !Mouse.down;
                }
            }
        };

        let rainbow = (x: number, y: number) => {
            let r = Math.sin(x + 0 * Math.PI / 3 * y) * 127 + 127;
            let g = Math.sin(x + 2 * Math.PI / 3 * y) * 127 + 127;
            let b = Math.sin(x + 4 * Math.PI / 3 * y) * 127 + 127;
            return "rgba(" + (r | 0) + ", " + (g | 0) + ", " + (b | 0) + ", 1)";
        };

        let animloop = () => {
            if (!ctx) return;

            requestAnimationFrame(animloop);
            renderer.render(scene, camera);

            let t = (Date.now() - start) / 1000;
            plane.geometry.attributes.position.needsUpdate = true;
            texture.needsUpdate = true;

            ctx.fillStyle = rainbow(t / 4, Math.cos(t / 30) * 0.45 + 0.5);
            ctx.fillRect(0, 0, canvas.width, canvas.height);

            for (let i = 0; i < plane.geometry.attributes.position.count; i++) {
                let vx = plane.geometry.attributes.position.getX(i);
                let vy = plane.geometry.attributes.position.getY(i);

                let vz = -Math.cos(vy * Math.PI + Mouse.y / canvas.height * Math.PI - Math.PI / 2) *
                    Math.cos(vx / camera.aspect * Math.PI - Mouse.x / canvas.width * Math.PI + Math.PI / 2) *
                    Math.sin(vx / camera.aspect * Math.PI + Math.PI / 2) *
                    Math.sin(vy * Math.PI + Math.PI / 2) *
                    0.01;

                plane.geometry.attributes.position.setZ(i, vz);
            }

            plane.rotation.y = -(Mouse.x / canvas.width - 0.5) * 0.02;
            plane.rotation.x = (Mouse.y / canvas.height - 0.5) * 0.02;
            plane.rotation.z = (Math.sin(Mouse.x / canvas.width * Math.PI - Math.PI / 2) + Math.cos(Mouse.y / canvas.height * Math.PI)) * Math.PI / 1080;

            camera.position.x = -(Mouse.x / canvas.width - 0.5) * 0.005;
            camera.position.y = (Mouse.y / canvas.height - 0.5) * 0.005;
            camera.position.z = Mouse.down ? 0.646 : 0.651;

            let spread = -2;

            // if (ctx.imageSmoothingEnabled !== false) {
            //     ctx.imageSmoothingEnabled = false;
            // }

            if (plane.material.wireframe === false) {
                ctx.drawImage(renderer.domElement, -spread / 2, -spread / 2, canvas.width + spread, canvas.height + spread);
            }

            if (!Mouse.down) return;

            let rx = (Math.random() - 0.5) * canvas.width / 30;
            let ry = (Math.random() - 0.5) * canvas.height / 30;

            ctx.strokeStyle = rainbow(2 * Math.PI * Math.random(), 1);
            ctx.beginPath();
            ctx.arc(Mouse.x + rx, Mouse.y + ry, 30 * Math.random(), 0, Math.PI * 2, false);
            ctx.closePath();
        };

        let resizeHandler = () => {
            canvas.width = window.innerWidth;
            canvas.height = window.innerHeight;
            renderer.setSize(window.innerWidth, window.innerHeight);
            camera.aspect = window.innerWidth / window.innerHeight;

            texture.dispose();
            geometry.dispose();
            geometry = new THREE.PlaneGeometry(camera.aspect, 1, 16, 16);
            texture = new THREE.Texture(canvas);
            texture.minFilter = THREE.LinearFilter;
            texture.magFilter = THREE.LinearFilter;
            material.map = texture;
        };

        window.addEventListener('resize', resizeHandler, true);
        window.addEventListener('mousemove', Mouse.events.move, true);
        window.addEventListener('mousedown', Mouse.events.down, true);
        window.addEventListener('mouseup', Mouse.events.up, true);

        resizeHandler();
        animloop();

        return () => {
            window.removeEventListener('resize', resizeHandler, true);
            window.removeEventListener('mousemove', Mouse.events.move, true);
            window.removeEventListener('mousedown', Mouse.events.down, true);
            window.removeEventListener('mouseup', Mouse.events.up, true);
        };
    }, []);

    return <canvas ref={canvasRef} {...props} className={`background-canvas ${className}`} />;
};

export default Component;